.contact {
  display: flex;
  max-height: 100vh;
  overflow: hidden;

  &__form {
    flex: 1;
  }

  &__image {
    flex: 1;
    background-image: url("../images/contactus.jpg");
    height: 100vh;
    background-size: cover;
    background-position: center;
    color: var(--color-white);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;

    &-opacity {
      width: 100%;
      height: 100vh;
      background: var(--color-contact-filter);
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      margin: 0;
    }

    & > h1,
    & > h3,
    & > &-details {
      margin-bottom: 2rem;
      padding: 0 10rem;
      z-index: 5;
      cursor: default;
    }

    & > &-details {
      text-align: center;
      margin: 3rem 0;

      i {
        font-size: 4rem;
        color: var(--color-hover);
      }

      h5 {
        font-size: 2.5rem;
      }

      p {
        font-size: 1.5rem;
      }
    }
  }
}
